import React from "react";
import { Select, Tag as AntTag } from "antd";
import { Tag } from "types";
import { useQuery } from "react-query3";
import { convertToAntTag } from "components/utils/convertToAntTag";

function tagRender(props) {
  const { label, value, closable, onClose, options } = props;

  return (
    <AntTag
      color={options.find(m => m.value === value).color}
      closable={closable}
      onClose={onClose}
      style={{ margin: 3 }}
    >
      {label}
    </AntTag>
  );
}

export default function TagSelectBox(props): JSX.Element {
  const { data: tags } = useQuery<Tag[]>("/tag", {
    refetchOnWindowFocus: false,
  });

  const options = tags ? convertToAntTag(tags) : props?.value;

  return (
    <Select
      {...props}
      showArrow
      labelInValue
      mode={"multiple"}
      maxTagCount="responsive"
      tagRender={(tagProps) =>
        tagRender({ ...tagProps, options })
      }
      style={{ width: "100%" }}
      options={options}
    />
  );
}
